}

.hero-container {
  display: grid;
  max-width: 100%;
  height: 100%;
  border:groove #A1A1A1 5px;
  grid-template-columns: repeat(3, minmax(80px, 1fr));
  grid-template-rows: repeat(3, auto);
  grid-gap: 1px
}

.hero-container div {
  border:outset #a41f28 3px;
  max-width: inherit;
  max-height: inherit;
}

.hero-container div img {
  box-shadow: 3px 3px 10px #a41f28;
