.History {
  padding:10px;
  padding-top: 2px;
  padding-bottom: 40px;
  color: white;
  padding-top:98px;
}

.history-container {
  border-radius: 10px;
  display: grid;
  max-width: 100%;
  height: 100%;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  grid-column-gap: 10px;
  grid-row-gap: 80px;
  text-align: center
}

.history-container .history-div {
    overflow: hidden;
    border: inset #898A8E 3px;
    border-radius: 10px;
    max-width: inherit;
    max-height: 100%
  }

.history-container .history-div .history-title {
        padding-bottom: 5px;
        border: ridge #A41F28 5px;
        margin-left: 30%;
        margin-right: 30%;
        padding: 8px;
        background: #A3000F
    }

.history-container .history-div .history-title h1 {
          border-left: 10px;
          border-left-style: groove ;
          border-left-color: #FF0018;
          margin:0;
          padding: 5px;
          color: #f0f1f1
        }

.history-container .history-div p {
      border: solid brown 2px;
      margin:8px;
      background: #212226;
      color: #D4D5D6
    }

.history-container .history-div .history-img {
      width: 100%;
      height: 100%;
    }

.history-container .history-div:nth-of-type(4) {
    grid-column:1 / 2;
    grid-row:2 / 3;
  }

.history-container .history-div:nth-of-type(3)  .history-title h1 {
      border-left: none;
      border-right: 10px;
      border-right-style: ridge ;
      border-right-color: #FF0018;
    }

